.MyOrder {
  width: 30%;
  padding: 30px;
  box-sizing: border-box;
  position: absolute;
  right: 0;
  background-color: white;
  top: 60px;
  bottom: 0;
  border-radius: 6px;
  border: 1px solid var(--very-light-pink);
}
.title-container {
  display: flex;
}
.title-container img {
  transform: rotate(180deg);
  margin-right: 14px;
  width: 20px;
  height: 20px;
}
.title {
  font-size: var(--lg);
  font-weight: bold;
}
.my-order-container {
  display: grid;
  grid-template-rows: auto 1fr auto;
  width: 300px;
}
.my-order-content {
  display: flex;
  flex-direction: column;
}
.order {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 20px;
  align-items: center;
  background-color: var(--text-input-field);
  margin-bottom: 24px;
  border-radius: 8px;
  padding: 24px;
  text-align: center;
  justify-content: center;
  // border: 3px solid rosybrown;
}
.order p:nth-child(1) {
  display: flex;
  flex-direction: column;
  text-align: center;
  // border: 3px solid rosybrown;
}
.order p span:nth-child(1) {
  font-size: var(--md);
  font-weight: bold;
  margin-top: 20px;
}
.order p span:nth-child(2) {
  font-size: var(--sm);
  color: var(--very-light-pink);
}
.order p:nth-child(2) {
  text-align: end;
  font-weight: bold;

  margin-top: 20px;
}
.shopping-cart {
  display: grid;
  grid-template-columns: auto 1fr auto auto;
  gap: 16px;
  margin-bottom: 24px;
  align-items: center;
}
.shopping-cart figure {
  margin: 0;
}
.shopping-cart figure img {
  width: 70px;
  height: 70px;
  border-radius: 20px;
  object-fit: cover;
}
.shopping-cart p:nth-child(2) {
  color: var(--very-light-pink);
  background-color: aquamarine;
}
.shopping-cart p:nth-child(3) {
  font-size: var(--md);
  font-weight: bold;
}
@media (max-width: 640px) {
  .MyOrder {
    width: 50%;
    padding: 30px;
  }
}
